body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-image: url(https://images.unsplash.com/photo-1501959915551-4e8d30928317?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1868&q=80);
  background-color: #cccccc;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0;
  padding: 0;
}

.graph__container {
  width: 1200px;
  height: 380px;
  margin: 0 auto;
  position: relative;
  background-color: transparent;
}

@media only screen and (max-width: 600px) {
  .graph__container {
    transform: translateY(8%);
    width: 310px;
    height: 360px;
    /* background-color:red; */
    /* transform: translateY(1%); */
  }
}

.graph__bar-chart {
  border: 2px solid #7b38d8;
  padding: 2px;
  max-width: 1200px;
  min-width: 300px;
  width: 100%;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.6);
}

@media only screen and (max-width: 600px) {
  .graph__bar-chart {
    border: none;
    background: transparent;
  }
}

.graph__header {
  padding: 10px;
}

.graph__chart-wrapper {
  max-width: 1200px;
  width: 100%;
  height: 320px;
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: row;
  /* justify-content: space-around; */
  align-items: flex-end;
  position: relative;
}

@media only screen and (max-width: 600px) {
  .graph__chart-wrapper {
    height: 290px;
  }
}

.graph__single-bar {
  flex-basis: 3.5%;
  /* height: 100%; */
  display: flex;
  height: 0px;
  flex-direction: row;
  justify-content: space-around;
  margin-right: 5.2px;
  align-items: flex-start;
  position: relative;
  background-color: transparent;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  transition: all .4s;
  background-color: orange;
  /* &--tool-tip::before {
      content: "dummy text";
      z-index: 10;
    position: absolute;
    visibility: visible;
    top: -100px;
    background-color: beige;
    border: 1px solid orangered;
    padding: 2px 1px;
    color: blue;
} */
}

.graph__single-bar:nth-child(even) {
  background-color: #7b38d8;
}

.graph__single-bar:nth-child(odd) {
  background-color: #9c73d4;
}

.graph__single-bar:hover {
  background-color: #f8db5a;
}

.graph__single-bar--visible {
  width: 70px !important;
  opacity: 1 !important;
}

.graph__single-bar span {
  opacity: 0;
  z-index: 1;
  width: 0;
  height: 70px;
  position: absolute;
  text-align: center;
  color: #202020;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: .8rem;
  bottom: 5px;
  right: -20px;
  background-color: #E0E0E0;
  padding: 3px 5px;
  border: 1px solid #7b38d8;
  border-radius: 5px;
  transition-property: all;
  transition-duration: .2s;
  transition-delay: .1s;
}

.graph__bar1 {
  height: 100px;
}

.graph__bar2 {
  height: 60px;
}

.graph__bar3 {
  height: 110px;
}

.graph__bar4 {
  height: 30px;
}

#delete-btn {
  background: none;
  color: black;
  border: 1px solid black;
  outline: none;
  border-radius: 3px;
  box-shadow: 0px 17px 10px -10px rgba(0, 0, 0, 0.4);
}

#delete-btn:hover {
  background-color: #7b38d8;
  color: white;
}

.primary-heading {
  text-align: center;
  color: #555555;
  text-decoration: none;
  font-weight: 100;
  font-size: 60px;
  letter-spacing: 1px;
  margin-top: 20px;
}

@media only screen and (max-width: 600px) {
  .primary-heading {
    /* position: absolute; */
    font-size: 30px;
    /* top: -20px;
            left: 5px; */
    /* margin-top: 36px; */
  }
}

.button {
  display: inline-block;
  background-color: #7b38d8;
  border-radius: 17px;
  border: none;
  outline: none;
  color: #fff;
  font-size: 17px;
  padding: 5px;
  width: 86px;
  transition: all .5s;
  cursor: pointer;
  margin: 2px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.button:hover {
  background-color: #a88dce;
}

@media only screen and (max-width: 600px) {
  .button {
    margin-top: 50px;
    width: 75px;
  }
}

input {
  display: block;
  border-radius: 12px;
  height: 50px;
  width: 200px;
  padding: 10px;
  outline: none;
  font-size: 70px;
  color: #222222;
  background: #f5f5f5;
  font-weight: 100;
  text-align: center;
  float: right;
}

@media only screen and (max-width: 600px) {
  input {
    float: none;
    /* background-color:blue; */
  }
}

.increment-btn {
  display: block;
  float: right;
  height: 35px;
  margin: -1px -2px -2px;
  width: 41px;
  margin: 0 1px;
  border-radius: 6px;
  font-size: 25px;
  color: grey;
  background-color: #f5f5f5;
  outline: none;
}

@media only screen and (max-width: 600px) {
  .increment-btn {
    margin: 10px;
    transform: translateY(30%);
  }
}

.decrement-btn {
  position: fixed;
  display: block;
  bottom: 0px;
  float: right;
  height: 35px;
  margin: -1px -2px -2px;
  width: 41px;
  margin: 0 1px;
  border-radius: 6px;
  font-size: 25px;
  color: grey;
  background-color: #f5f5f5;
  outline: none;
}

.side-button-container {
  /* float: left;
    margin-right: 5px; */
  display: inline;
}

.interface-container {
  /* margin: 0 auto; */
  /* text-align: center; */
  margin-bottom: 175px;
}

.input-container {
  margin-top: 132px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-56%, -463%);
}

@media only screen and (max-width: 600px) {
  .input-container {
    margin-top: 0px;
    /* background-color:red; */
    transform: translate(-56%, -170%);
  }
}

.weight-input {
  margin: 0 auto;
}

.increments-container {
  padding: 5px;
}

.bottom-button-container {
  width: 186px;
  position: absolute;
  margin-top: 60px;
