.plural {
  opacity: 0;
}

.diffDisplay {
  font-size: 1.5em;
  position: absolute;
  font-weight: 300;
  color: #5e5d5d;
  right: 100px;
  top: -8px;
  /* clear: both; */
}

@media only screen and (max-width: 600px) {
  .diffDisplay {
    font-size: 1em;
    font-weight: 600;
  }
}

.kg {
  font-size: 1.5em;
  position: absolute;
  font-weight: 300;
  color: #5e5d5d;
  right: 16px;
  top: -8px;
}

@media only screen and (max-width: 600px) {
  .kg {
    font-size: 1em;
    font-weight: 600;
  }
}

.difference {
  display: flex;
  font-size: 1.5em;
  font-weight: 300;
  color: #5e5d5d;
  position: absolute;
  /* margin: 0px 15px 0px 0px; */
  /* width:30px; */
  /* height: 30px; */
  top: 16px;
  right: 20px;
  /* transform: translateX(-48px); */
  /* border: solid 2px red; */
}

.difference::after {
  content: " kg";
  color: #5e5d5d;
  font-weight: 300;
}

@media only screen and (max-width: 600px) {
  .difference::after {
