  }
}

.difference--positive {
  color: #1cb01c;
}

.difference--negative {
  color: red;
}

@media only screen and (max-width: 600px) {
  .difference {
    font-size: 1em;
    top: 9px;
    right: 55px;
    font-weight: 600;
  }
}
